import axios from 'axios';

const API_URL = '';

export const fetchMangaStats = async () => {
  try {
    const response = await fetch('/api/manga/stats');
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return await response.json();
  } catch (error) {
    console.error('Помилка при отриманні статистики манги:', error);
    return null;
  }
};

export const fetchFilterOptions = async () => {
  try {
    const response = await axios.get(`${API_URL}/manga/filter-options`);
    return response.data;
  } catch (error) {
    console.error('Error fetching filter options:', error);
    return { genres: [], teams: [], types: [] };
  }
};

