import React, { useState, useEffect } from 'react';
import StatisticsDisplay from './components/StatisticsDisplay';
import { fetchMangaStats } from './services/api';
import './App.css'; // Make sure you have this CSS file

function App() {
  const [stats, setStats] = useState(null);

  useEffect(() => {
    if (window.__INITIAL_DATA__) {
      setStats(window.__INITIAL_DATA__);
    } else {
      fetchMangaStats().then(data => setStats(data));
    }
  }, []);

  return (
    <div className="App">
      <div className="container">
        <h1>Статистика miu</h1>
        {stats ? <StatisticsDisplay stats={stats} /> : <p>Завантаження статистики...</p>}
      </div>
    </div>
  );
}

export default App;