import React, { useMemo } from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title } from 'chart.js';
import { Pie, Bar } from 'react-chartjs-2';
import './StatisticsDisplay.css'; // Make sure you have this CSS file

// Register the Chart.js components
ChartJS.register(
  ArcElement, 
  Tooltip, 
  Legend, 
  CategoryScale, 
  LinearScale, 
  BarElement, 
  Title
);

const StatisticsDisplay = React.memo(({ stats }) => {
  const { genreCounts, typeCounts, teamCounts, teamGenreCorrelation } = stats;

  const genreData = useMemo(() => ({
    labels: genreCounts.map(item => item._id),
    datasets: [{
      data: genreCounts.map(item => item.count),
      backgroundColor: [
        'rgba(255, 99, 132, 0.6)',
        'rgba(54, 162, 235, 0.6)',
        'rgba(255, 206, 86, 0.6)',
        'rgba(75, 192, 192, 0.6)',
        'rgba(153, 102, 255, 0.6)',
        'rgba(255, 159, 64, 0.6)',
      ],
    }],
  }), [genreCounts]);

  const typeData = useMemo(() => ({
    labels: typeCounts.map(item => item._id),
    datasets: [{
      label: 'Кількість коміксів за типом',
      data: typeCounts.map(item => item.count),
      backgroundColor: 'rgba(75, 192, 192, 0.6)',
    }],
  }), [typeCounts]);

  const teamData = useMemo(() => ({
    labels: teamCounts.map(item => item._id),
    datasets: [{
      label: 'Кількість коміксів за командою',
      data: teamCounts.map(item => item.count),
      backgroundColor: 'rgba(153, 102, 255, 0.6)',
    }],
  }), [teamCounts]);

  const teamGenreData = useMemo(() => {
    const allGenres = Array.from(new Set(teamGenreCorrelation.flatMap(team => team.genres.map(g => g.genre))));
    return {
      labels: teamGenreCorrelation.map(item => item._id),
      datasets: allGenres.map((genre, index) => ({
        label: genre,
        data: teamGenreCorrelation.map(team => {
          const genreData = team.genres.find(g => g.genre === genre);
          return genreData ? genreData.count : 0;
        }),
        backgroundColor: `rgba(${index * 40 % 255}, ${index * 70 % 255}, ${index * 100 % 255}, 0.6)`,
      }))
    };
  }, [teamGenreCorrelation]);

  const commonOptions = {
    responsive: true,
    maintainAspectRatio: false,
  };

  const genreOptions = {
    ...commonOptions,
    plugins: {
      legend: {
        position: 'right',
      },
    },
  };

  const typeOptions = {
    ...commonOptions,
    indexAxis: 'x',
    plugins: {
      legend: {
        display: false,
      },
    },
    scales: {
      x: {
        ticks: {
          autoSkip: false,
          maxRotation: 0,
          minRotation: 0
        }
      }
    }
  };

  const teamOptions = {
    ...commonOptions,
    indexAxis: 'y',
    plugins: {
      legend: {
        display: false,
      },
    },
  };

  const stackedBarOptions = {
    ...commonOptions,
    indexAxis: 'y',
    scales: {
      x: {
        display: false,  // This hides the scale
        stacked: true,
      },
      y: {
        stacked: true
      }
    },
    plugins: {
      legend: {
        display: false,  // This removes the legend
      },
      tooltip: {
        callbacks: {
          label: function(context) {
            let label = context.dataset.label || '';
            if (label) {
              label += ': ';
            }
            if (context.parsed.x !== null) {
              label += context.parsed.x;
            }
            return label;
          }
        }
      }
    },
  };

  return (
    <div className="statistics-display">
      <div className="chart-item">
        <h3>Розподіл жанрів (30+ записів)</h3>
        <div className="chart-wrapper">
          <Pie data={genreData} options={genreOptions} />
        </div>
      </div>
      <div className="chart-item">
        <h3>Кількість коміксів за типом</h3>
        <div className="chart-wrapper">
          <Bar data={typeData} options={typeOptions} />
        </div>
      </div>
      <div className="chart-item">
        <h3>Топ-20 команд за кількістю коміксів</h3>
        <div className="chart-wrapper">
          <Bar data={teamData} options={teamOptions} />
        </div>
      </div>
      <div className="chart-item">
        <h3>Топ-20 команд - Кореляція жанрів</h3>
        <div className="chart-wrapper">
          <Bar data={teamGenreData} options={stackedBarOptions} />
        </div>
      </div>
    </div>
  );
});

export default StatisticsDisplay;